<template>
    <div class="PayOrder">
      <HomePageTop :TopTitle="TopTotle" class="bg_fff"></HomePageTop>
      <div class="classify">
        <p class=" f28 cl_666 center">付款合计</p>
        <p class="f48 mt20 pb30 border_E3E4E8 center"><span class="f40">￥</span>{{depreciate}}</p>
        <div class="specific" >
          <div class="border_E3E4E8">
            <div class="fiex"><p>
              <i class="iconfont cl_40BA49 mr14">&#xe62a;</i>微信支付
            </p>
              <span class="cl_f90 Yes">√</span></div>
            <div class="reveal f28">
              <i class="bg_icons dib vm icon_bg"></i>
              <div class="dib vm"><span>余额付款:</span> <p class="cl_f90 dib">-￥<span class="money">23.56</span><span class="cl_999">（可用余额：￥23.56)</span></p> </div>
            </div>
          </div>
          <div class="border_E3E4E8">
            <div class="fiex">
              <p>
              <i class="iconfont cl_40BA49 mr14">&#xe602;</i>
                支付宝支付
              </p>
              <span class="cl_f90 Yes">√</span>
            </div>
            <div class="reveal f28">
              <i class="bg_icon dib vm icon_bg"></i>
              <div class="dib vm"><span>余额付款:</span> <p class="cl_f90 dib">-￥<span class="money">23.56</span><span class="cl_999">（可用余额：￥23.56)</span></p> </div>

            </div>
          </div>
          <div class="credit border_E3E4E8">
            <div class="fiex"><p><i class="iconfont cl_40BA49 mr14">&#xe626;</i>信用支付</p> <span class="cl_f90 Yes">√</span></div>
            <p class="f28 cl_999">可用信用余额:¥4090.14</p>
          </div>
        </div>
      </div>
      <div class="public-btn mt60  mr30 ml30" @click="jump">
        确认付
      </div>
    </div>
</template>
<script>
    import Vue from "vue"
    import $ from "jquery"
    import HomePageTop from "../../common/HomePageTop";

    export default {
        name: "PayOrder",
        components: {HomePageTop},
        data() {
            return {
              //像子组件传值
              TopTotle: {name: "支付订单",left: true},
              //降价
              depreciate:"0.00",
              totalPrices:1000,
            }
        },
        methods: {
          //跳转
          jump(){
            this.$router.push({
              path:"BagOrder"
            })
          },
        },
        mounted(){
          var _this=this;
          $(()=>{
            let jqqian=$($(".specific .border_E3E4E8")[0]).find(".money").text();
            _this.$data.depreciate=_this.$data.totalPrices - jqqian;
            $(".specific").find(".reveal").hide();
            $($(".specific .border_E3E4E8")[0]).find(".Yes").show();
            $( $(".specific .border_E3E4E8").find(".reveal")[0]).show();
            $(".specific .border_E3E4E8").click(function(event){
              $(this).find(".Yes").show();
              $(this).find(".reveal").show();
              $(this).siblings().find(".Yes").hide();
              $(this).siblings().find(".reveal").hide();
              $(this).siblings().find(".icon_bg").removeClass("bg_icons");
              $(this).find(".icon_bg").addClass("bg_icons");
              event.stopPropagation();


              //先判断按钮是否已经点击              已经点击
              console.log($(this).find(".icon_bg").hasClass("icon_bg"));
            });
            $(".reveal").click(function(event){
              event.stopPropagation();
              console.log($(this).find(".icon_bg").hasClass("bg_icons"));
              if($(this).find(".icon_bg").hasClass("bg_icons")){
                $(this).find(".icon_bg").addClass("bg_icon");
                $(this).find(".icon_bg").removeClass("bg_icons");
                _this.$data.depreciate=Number(_this.$data.depreciate) + Number($(this).find(".money").text());
              }else{
                $(this).find(".icon_bg").addClass("bg_icons");
                $(this).find(".icon_bg").removeClass("bg_icon");
                _this.$data.depreciate=Number(_this.$data.depreciate) - Number($(this).find(".money").text());
              }





            })

          })


        },
        created() {

        }
    }

</script>

<style lang="less">
  html,body{
    height: 100%;
    background: #fff;
  }
  .Yes{
    display: none;
  }
  .PayOrder{
    background: #fff;height: 100%;
    .classify{
      height: 100%;
      background: #fff;
      padding-top: .3rem;
      .specific{
        padding: 0 .3rem;
        .reveal{
          padding-left: .45rem;
          i{
            width: .4rem;
            height: .4rem;
          }
        }
        .fiex{
          justify-content: space-between;    display: flex;
        }
        .border_E3E4E8{

          font-size: .34rem;
          padding: .3rem 0;
          color: #333;
        }
        .credit{
          border-bottom: 1px solid #E3E4E8;
          padding: .3rem 0;
          color: #333;
          font-size: .34rem;

          div{
            justify-content: space-between;    display: flex;
          }

        }
      }

    }

  }

.bg_icon{background: url("../../../assets/img/common/Nopitch.png") no-repeat;background-size: cover;}
.bg_icons{background: url("../../../assets/img/common/pitch.png") no-repeat; background-size: cover;}

</style>



